.Menu {
  /* border: 2px solid lightslategray; */
  padding: 5px;
}

.Menu__list {
  display: -ms-flexbox;
  display: flex;

  -ms-flex-pack: center;

      justify-content: center;
  padding: 0;
  margin: 0;
  list-style: none;
}

.Menu__list li {
  margin: 0 5px 0 5px;
}

.Menu__link {
  border: 3px solid lightslategray;
  display: block;
  padding: 10px;
  color: #000;
  text-decoration: none;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}

.Menu__link:hover {
  border-color: lightseagreen;
}


